import { Component, inject } from '@angular/core';

import { UserSessionService } from 'src/app/core/session/user-session.service';
import { SessionGroupsModel } from 'src/app/api/model/models';

import { SessionService } from '../session.service';
import { SearchService } from './search.service';

@Component({
  selector: 'search',
  standalone: false,
  templateUrl: './search.component.html',
  styleUrl: './search.component.scss'
})
export class SearchComponent {
  private readonly searchService = inject(SearchService);
  private readonly sessionService = inject(SessionService);
  private readonly userSession = inject(UserSessionService);

  loggedInUserId: number;
  searchMode: string = '';

  isFirstTimeDialogOpen = true;
  isLoading = false;

  constructor() {
    this.loggedInUserId = this.userSession.userId;
  }

  get sessionsList(): SessionGroupsModel[] {
    return this.sessionService.sessionsList;
  }

  get sessionExpandedList() {
    return this.sessionService.sessionExpandedList;
  }

  get isFirstSearch() {
    return this.sessionService.isFirstSearch;
  }

  set isFirstSearch(value: boolean) {
    this.sessionService.isFirstSearch = value;
  }

  onLoadingEvent(state: boolean) {
    this.searchMode = 'Basic';
    this.isLoading = state;
  }

  onSessionSelect(event: HTMLInputElement, id: string) {
    this.searchService.updateSelectedSessions(id, event.checked ? 'add' : 'remove');
  }

  ngOnInit() {
    // * To collapse all the expanded session cards when coming from the details page
    this.sessionService.sessionExpandedList = new Array(this.sessionService.sessionExpandedList.length).fill(false);
  }

  ngOnDestroy() {}
}
