import { Component, ElementRef, inject, input, ViewChild } from '@angular/core';

import { SessionConstants } from 'src/app/constants/session-constants';
import { SessionGroupsModel } from 'src/app/api/model/models';

import { CalculationHelpers } from 'src/app/modules/shared/helpers/calculations';
import { SessionService } from '../../session.service';

import { FileCardComponent } from '../file-card/file-card.component';
import { GoogleMapsService } from 'src/app/modules/shared/services/google-maps.service';

@Component({
  selector: 'session-card',
  standalone: false,
  templateUrl: './session-card.component.html',
  styleUrl: './session-card.component.scss',
  host: {
    '(document:click)': 'closeDropdown($event)'
  }
})
export class SessionCardComponent {
  private readonly el = inject(ElementRef);
  private readonly sessionService = inject(SessionService);
  private readonly googleMapsService = inject(GoogleMapsService);
  private readonly calculationHelpers = inject(CalculationHelpers);

  @ViewChild(FileCardComponent) fileCardComponent!: FileCardComponent;

  $session = input.required<SessionGroupsModel>({ alias: 'session' });
  $isDropdownExpanded = input.required<boolean>({ alias: 'isDropdownExpanded' });

  mutationObserver: MutationObserver | null = null;
  readonly sessionTypes = SessionConstants.sessionTypes;

  $isMapLoaded = this.googleMapsService.api_loaded;
  mapOptions: google.maps.MapOptions = {};
  mapMarkers: { lat: number; lng: number }[] = [];
  mapVisible = false;

  isTooltipDisabled = true;
  dropdownState = false;
  overflowExpanded = false;
  overflowContents: string[] = ['RTAC'];

  get username(): string {
    return this.$session()['<CreatorUser>k__BackingField']._username;
  }

  get filesLength(): number {
    // ! Exclude failed files
    const excludedFiles = this.$session()._recordings.filter((file) => file['<RedactedStatus>k__BackingField'] === 11);

    return this.$session()._recordings.length + this.$session()._photos.length - excludedFiles.length;
  }

  get duration(): string {
    const start: any = new Date(this.$session()._start_time);
    const end: any = new Date(this.$session()._finish_time);

    const diff = end.getTime() - start.getTime();
    const calculated_time = this.calculationHelpers.secondsToTime(diff / 1000);

    const final_time = this.calculationHelpers.timeToWords(calculated_time);
    return final_time;
  }

  get containsRedactedFiles(): boolean {
    const files = this.$session()._recordings.filter(
      (rec) => rec['<RedactedStatus>k__BackingField'] !== -1 && rec['<RedactedStatus>k__BackingField'] !== 13
    );

    return files.length !== 0;
  }

  get sessionType(): number {
    return this.$session()['<SessionType>k__BackingField'];
  }

  onDropdownToggle() {
    this.dropdownState = !this.dropdownState;
    this.sessionService.updateSessionList(this.$session()!._session_id, this.dropdownState);
  }

  expandOverflow(event: MouseEvent) {
    event.stopPropagation();
    this.overflowExpanded = !this.overflowExpanded;
  }

  closeDropdown(event: any) {
    const clickedInsideComponent = this.el.nativeElement.contains(event.target);
    // const clickedInsideMultiSelectDropdown = !!(event.target as HTMLElement).closest('.select-drop-wrap');

    // * Condition 1: Check for multi-select & close only when clicked outside
    // * Condition 2: Check for single select & close when clicked outside and inside the dropdown
    if (!clickedInsideComponent) {
      if (this.overflowExpanded) this.overflowExpanded = false;
    }
  }

  onOverflowClick(event: MouseEvent) {
    event.stopPropagation();
  }

  routeToDetails() {
    // ! Will not work for now
    // TODO: Implement the logic to route when clicking on a session. Check file visibility & accessibility also.
    this.fileCardComponent.routeToDetails();
  }

  displayMap(state: boolean) {
    this.mapVisible = state;
  }

  ngAfterViewInit(): void {
    // * This is a workaround to remove the keyboard shortcuts dialog that appears on the map - BUG
    this.mutationObserver = new MutationObserver(() => {
      const dialogs = document.querySelectorAll('dialog');

      dialogs.forEach((dialog) => {
        const isShortcutDialog = dialog.textContent?.includes('Keyboard shortcuts');

        if (isShortcutDialog) {
          // - Safely close if still open
          if ((dialog as HTMLDialogElement).open) {
            try {
              (dialog as HTMLDialogElement).close();
            } catch (err) {
              console.warn('Dialog close failed:', err);
            }
          }
        }
      });
    });

    // - Observe the entire document for changes
    this.mutationObserver.observe(document.body, {
      childList: true,
      subtree: true
    });
  }

  async ngOnInit() {
    await this.googleMapsService.initialize();

    this.mapOptions = {
      mapId: Math.random().toString(36).substring(2, 15),
      center: { lat: -33.71, lng: 150.36 },
      zoom: 13,
      disableDefaultUI: true,
      keyboardShortcuts: false
    };

    // TODO: Update this with data from API
    this.mapMarkers = [
      // { lat: -31.56391, lng: 147.154312 },
      { lat: -33.718234, lng: 150.363181 }
      // { lat: -34.327111, lng: 155.371124 }
    ];
  }

  ngOnDestroy() {
    if (this.mutationObserver) this.mutationObserver.disconnect();
  }
}
