import { Component, inject, output, QueryList, ViewChild, ViewChildren } from '@angular/core';
import { DatePipe } from '@angular/common';
import { MatDialog } from '@angular/material/dialog';
import { Subject, takeUntil } from 'rxjs';

import { UserSessionService } from 'src/app/core/session/user-session.service';
import { SessionData } from 'src/app/data/session.data';

import { SessionService } from 'src/app/modules/media/session/session.service';
import { SearchFormData, SearchService } from '../../search/search.service';

import { CategoryApiService, OrganizationApiService, SessionApiService, UserApiService } from 'src/app/api';
import { CategoriesModel, OrganizationModel, SessionSearchResponseModel, UserModel } from 'src/app/api/model/models';

import { AdvancedFilterComponent } from '../advanced-filter/advanced-filter.component';
import { DatePickerComponent } from 'src/app/modules/shared/components/date-picker/date-picker.component';
import { SelectSimpleFieldComponent } from 'src/app/modules/shared/components/select-simple-field/select-simple-field.component';
import { AddCaseComponent } from '../add-case/add-case.component';

@Component({
  selector: 'basic-filter',
  standalone: false,
  templateUrl: './basic-filter.component.html',
  styleUrl: './basic-filter.component.scss'
})
export class BasicFilterComponent {
  private readonly dialog = inject(MatDialog);
  private readonly datePipe = inject(DatePipe);
  private readonly searchService = inject(SearchService);
  private readonly sessionApiService = inject(SessionApiService);
  private readonly userSession = inject(UserSessionService);
  private readonly sessionService = inject(SessionService);
  private readonly organizationApiService = inject(OrganizationApiService);
  private readonly categoryApiService = inject(CategoryApiService);
  private readonly userApiService = inject(UserApiService);

  @ViewChild(DatePickerComponent) datePicker!: DatePickerComponent;
  @ViewChildren(SelectSimpleFieldComponent) selectComponent!: QueryList<SelectSimpleFieldComponent>;

  onSearchEvent = output<{
    fromDate: Date;
    toDate: Date;
    searchInput: string;
  }>();
  onLoadingEvent = output<boolean>();

  private destroy$: Subject<void> = new Subject<void>();
  $formdata = this.searchService.formData;

  organizationList: string[] = SessionData.organizationList;
  organizationIdList: number[] = [-1]; // For company IDs

  accountList: string[] = SessionData.accountList;
  accountIdList: number[] = [-1]; // For account/user IDs

  categoryList: string[] = SessionData.categoryList;
  categoryIdList: number[] = [-1]; // For category IDs

  loggedInUserId: number;
  isLoading = false;
  saveSearchList = ['string', 'next', 'after'];
  searchResultText = '';

  currentPage: number = 1;
  itemsPerPage: number = 30;
  isPaginationEnabled = false;

  constructor() {
    this.loggedInUserId = this.userSession.userId;
  }

  get canAddCase(): boolean {
    return this.searchService.selectedSessions().length !== 0;
  }

  get isFirstSearch() {
    return this.sessionService.isFirstSearch;
  }

  get isFormValid() {
    const isValid =
      this.$formdata().date !== '' &&
      this.searchService.dateRange().start &&
      this.searchService.dateRange().end &&
      this.$formdata().user !== '';

    return isValid;
  }

  get canSaveAndReset(): boolean {
    return !this.searchService.isUnchanged();
  }

  get totalItems(): number {
    return this.sessionService.totalSessionCount;
  }

  toggleAllFilters() {
    const dialog = this.dialog.open(AdvancedFilterComponent, {
      panelClass: 'all-filters-dialog',
      disableClose: false
    });

    dialog.afterClosed().subscribe((result) => {
      if (result) this.testFilterSessions();
    });
  }

  toggleAddToCase() {
    if (!this.canAddCase) return;

    const dialog = this.dialog.open(AddCaseComponent, {
      disableClose: false
    });

    dialog.afterClosed().subscribe((result) => {
      if (result) console.log('Add case');
    });
  }

  handleDateChange(data: { val: string; start: Date | undefined; end: Date | undefined }) {
    this.searchService.updateFormData({
      date: data.val
    });

    this.searchService.updateDateRange({ start: data.start, end: data.end });
  }

  handleMultiSelectDrop(key: keyof SearchFormData, data: string) {
    const existing_list: string[] = this.$formdata()[key] as string[];

    if (!existing_list.includes(data)) {
      existing_list.push(data);
    } else {
      const index = existing_list.indexOf(data);
      existing_list.splice(index, 1);
    }

    this.searchService.updateFormData({
      [key]: existing_list
    });
  }

  resetFilters() {
    if (!this.canSaveAndReset) return;

    this.searchService.resetForm('basic');
    this.datePicker.resetFields();
    this.selectComponent.forEach((comp) => comp.resetFields());
  }

  paginate(page_number: number) {
    this.currentPage = page_number;
    this.testFilterSessions();
  }

  filterSessions() {
    const current_date = new Date();

    const few_days_back = new Date(); // to get 7 days before
    few_days_back.setDate(current_date.getDate() - 7);

    this.isLoading = true;
    this.onLoadingEvent.emit(this.isLoading);

    this.sessionApiService
      .apiMediaSearchGetFilterMediaSearch({
        currentPageNo: 1,
        itemsPerPage: 20,
        searchMode: 'Basic',
        userID: -1, // -1 for "All", 17134 || User ID is the Account
        loggedInUserID: this.loggedInUserId,
        combinedSearch: '',
        from: few_days_back.toISOString(),
        to: current_date.toISOString(),
        includeExpired: true,
        includeLiveSessions: true
      })
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (response: SessionSearchResponseModel) => {
          // Set session data
          this.sessionService.totalSessionCount = response.TotalSessionsCount;
          this.sessionService.sessionsList = response.SessionGroups;
          this.sessionService.sessionExpandedList = [...new Array(response.SessionGroups.length).fill(false)];

          // this.onSearchEvent.emit({
          //   fromDate: this.selectedFromDate!,
          //   toDate: this.selectedToDate!,
          //   searchInput: this.$formdata().freeText
          // });

          // Set Loaders
          this.isLoading = false;
          this.onLoadingEvent.emit(this.isLoading);

          // Reset form
        },
        error: (error: Error) => {
          console.log('Session basic search API error: ', error);

          this.isLoading = false;
          this.onLoadingEvent.emit(this.isLoading);
        }
      });
  }

  testFilterSessions(firstSearch: boolean = false) {
    this.isPaginationEnabled = false;
    this.onLoadingEvent.emit(true);

    if (firstSearch) {
      setTimeout(() => {
        this.sessionService.totalSessionCount = 4;
        this.sessionService.sessionsList = SessionData.sessionList.SessionGroups.filter((session, i) => {
          if (i < 4) return session;
        });
        this.sessionService.sessionExpandedList = new Array(4).fill(false);
        this.onLoadingEvent.emit(false);

        this.searchResultText = `
          <span class="text-primary-charcoal fw-medium">Last 7 days</span> of recordings
        `;

        this.isPaginationEnabled = true;
      }, 1000);
    } else {
      setTimeout(() => {
        this.sessionService.totalSessionCount = 20;
        this.sessionService.sessionsList = SessionData.sessionList.SessionGroups;
        this.sessionService.sessionExpandedList = new Array(20).fill(false);

        this.onLoadingEvent.emit(false);

        const selectedDate = this.$formdata().date;
        switch (selectedDate) {
          case 'Today':
            this.searchResultText = `
              <span class="text-primary-charcoal fw-medium">Today's</span> recordings
            `;
            break;

          case 'Yesterday':
            this.searchResultText = `
              <span class="text-primary-charcoal fw-medium">Yesterday's</span> recordings
            `;
            break;

          case 'Last 7 days':
            this.searchResultText = `
              <span class="text-primary-charcoal fw-medium">Last 7 days</span> recordings
            `;
            break;

          case 'Last 30 days':
            this.searchResultText = `
              <span class="text-primary-charcoal fw-medium">Last 30 days</span> recordings
            `;
            break;

          default:
            const start = this.datePipe.transform(this.searchService.dateRange().start, 'dd MMMM y');
            const end = this.datePipe.transform(this.searchService.dateRange().end, 'dd MMMM y');

            this.searchResultText = `
                Recordings from 
                  <span class="text-primary-charcoal fw-medium">${start}</span>
                to
                  <span class="text-primary-charcoal fw-medium">${end}</span> 
              `;
            break;
        }

        this.isPaginationEnabled = true;
      }, 1000);
    }
  }

  ngOnInit() {
    // * First search for last 7 days content
    if (this.isFirstSearch) {
      const then = new Date();
      const now = new Date();
      then.setDate(then.getDate() - 7);

      this.searchService.updateDateRange({
        start: then,
        end: now
      });
      // this.filterSessions();
    }

    this.testFilterSessions(true);
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
