  selectedFromDateModel!: Date;
  selectedToDateModel!: Date;

  organizationList: string[] = SessionData.organizationList;
  organizationIdList: number[] = [0]; // For company IDs

  accountList: string[] = SessionData.accountList;
  accountIdList: number[] = [0]; // For account/user IDs

  categoryList: string[] = SessionData.categoryList;
  categoryIdList: number[] = [0]; // For category IDs

  bwvList: string[] = ['Mesh MTS', 'Mesh Camera']; // ? Is API call for this necessary
  bwvIdList: string[] = []; // For bwv IDs

