import { Component, ElementRef, inject, input, signal, TemplateRef } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';

@Component({
  selector: 'save-search',
  standalone: false,
  templateUrl: './save-search.component.html',
  styleUrl: './save-search.component.scss',
  host: {
    '(document:click)': 'closeDropdown($event)'
  }
})
export class SaveSearchComponent {
  private readonly el = inject(ElementRef);
  private readonly dialog = inject(MatDialog);

  $saveSearchList = input.required<string[]>({ alias: 'saveSearchList' });
  $customClass = input<string>('', { alias: 'customClass' });
  $disabled = input.required<boolean>({ alias: 'disabled' });

  $isSaveSearchExpanded = signal<boolean>(false);
  $selectedSavedSearch = signal<string>('');

  constructor() {}

  openDialog(templateRef: TemplateRef<any>) {
    if (this.$disabled()) return;

    this.dialog.open(templateRef, {
      width: '360px'
    });
  }

  expandSaveSearch() {
    this.$isSaveSearchExpanded.update((prev) => !prev);
  }

  getSelectedItem(item: any) {
    this.$selectedSavedSearch.set(item);
  }

  closeDropdown(event: any) {
    if (!this.el.nativeElement.contains(event.target)) {
      if (this.$isSaveSearchExpanded()) this.$isSaveSearchExpanded.set(false);
    }
  }
}
