import { Injectable, signal } from '@angular/core';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class GoogleMapsService {
  private $api_loaded = signal<boolean>(false);
  api_loaded = this.$api_loaded.asReadonly();

  private loadPromise?: Promise<void>;

  initialize(): Promise<void> {
    // * Reuse the same promise if already loading or loaded
    if (this.loadPromise) return this.loadPromise;

    this.loadPromise = new Promise((resolve, reject) => {
      // - Check if the API is already loaded
      if (this.$api_loaded()) {
        resolve();
        return;
      }

      const script = document.createElement('script');

      // - Load Google Maps API
      script.src = `https://maps.googleapis.com/maps/api/js?key=${environment.googleMapsApiKey}&libraries=places`;

      // - async and defer attribute configuration
      script.async = true;
      script.defer = true;

      // - Load the API and resolve the promise
      script.onload = () => {
        this.$api_loaded.set(true);
        resolve();
      };

      // - Handle error
      script.onerror = () => {
        this.loadPromise = undefined;
        reject('Google Maps API failed to load.');
      };

      // - Append the script to the document head
      document.head.appendChild(script);
    });

    return this.loadPromise;
  }
}
